import React, { useState, useEffect } from 'react'
import axios from 'axios'
// Layouts
import Layout from '../layouts/Layout'
// Components
import CreateSteps from '../components/Create/CreateSteps'
import SetupReward from '../components/Create/SetupReward'
// Constants
import { NULL_ADDRESS } from '../utils/constants'

export const STEPS = {
  REWARD_SETUP: 'REWARD_SETUP',
  REQUIREMENTS_SETUP: 'REQUIREMENTS_SETUP',
  DEPLOYMENT: 'DEPLOYMENT',
}

const CreatePage = (props) => {
  const [step, setStep] = useState(STEPS.REWARD_SETUP)
  const [assets, setAssets] = useState([])
  const [contracts, setContracts] = useState({})

  useEffect(async () => {
    try {
      const { data } = await axios.get(`http://35.229.209.76/api/v1/contracts?limit=300&offset=0`)
      setContracts(data.contracts.reduce((p, { contractAddress, ...c }) => {
        // eslint-disable-next-line no-param-reassign
        p[contractAddress] = c
        return p
      }, {}))
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error(e)
    }

    return () => {}
  }, [0])
  
  useEffect(async () => {
    if (props.selectedAddress !== NULL_ADDRESS) {
      try {
        const { data } = await axios.get(
          `http://35.229.209.76/api/v1/assets?ownerAddress=${props.selectedAddress}`,
        )
        setAssets(data.assets)
      } catch (e) {
        // eslint-disable-next-line no-console
        console.error(e)
      }
    }

    return () => {}
  }, [props.selectedAddress])

  return <Layout {...props}>
    <CreateSteps step={step} />
    <SetupReward />
  </Layout>
}

export default CreatePage